.header-burger {
    width: 35px;
    height: 30px;

    span {
        position: relative;

        display: none;
        width: 100%;
        height: 6px;

        background-color: #e65c00;
        border-radius: 5px;
        box-shadow: 0px 0 5px 2px #fff;

        &::before,
        &::after {
            content: "";

            position: absolute;
            top: 20px;
            left: 0;
            right: 0;

            width: 100%;
            height: 6px;

            background-color: #e65c00;
            box-sizing: border-box;
            border-radius: 5px;
            box-shadow: 0px 0 5px 2px #fff;
        }

        &::after {
            top: 10px;
        }

        @media(max-width: $mobile-width) {
            display: block;
        }
    }
}

.header {
    &.active {
        .header-burger {
            position: absolute;

            top: 50px;
            right: 7px;
        }

        span {
            transform: rotate(45deg);
            box-shadow: none;

            &::before {
                top: 0;
                transform: rotate(-90deg);
                box-shadow: none;
            }

            &::after {
                top: 0;
                transform: rotate(90deg);
                box-shadow: none;
            }
        }
    }
}
