.contacts-item {
    position: relative;

    max-width: 300px;
    padding-top: 130px;

    text-align: center;

    &::before {
        content: "";

        position: absolute;
        top: 0;
        left: 50%;

        width: 100%;
        height: 114px;

        transform: translateX(-50%);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }

    &::after {
        content: "";

        position: absolute;
        top: -80px;
        left: 50%;

        width: 130%;
        height: 140%;

        background-image: url('../../img/contacts/item-bg.png');
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        transform: translateX(-50%);
        opacity: 0;
        pointer-events: none;
        transition: 0.5s;
    }

    &:hover::after {
        opacity: 1;
    }

    @media(max-width: $tablet-width) {
        overflow: hidden;
    }

    @media(max-width: $mobile-width) {
        max-width: 100%;
        margin-bottom: 10px;
        padding: 0;

        text-align: left;

        &::before,
        &::after {
            display: none;
        }
    }
}

.contacts-item--support::before {
    background-image: url('../../img/contacts/support.png');
}

.contacts-item--job::before {
    background-image: url('../../img/contacts/job.png');
}

.contacts-item--hello::before {
    background-image: url('../../img/contacts/hello.png');
}

.contacts-item__title {
    margin: 0;
    margin-bottom: 10px;

    font-size: 20px;

    @media(max-width: $mobile-width) {
        margin-bottom: 0;
    }
}

.contacts-item__mail {
    position: relative;

    color: #2b2b2b;
    font-size: 20px;
    text-decoration: none;

    @media(max-width: $mobile-width) {
        color: $bg;
    }

    &::after {
        content: "";

        position: absolute;
        bottom: -3px;
        left: 0;

        width: 100%;
        height: 2px;
        background-color: #2b2b2b;
        transition: 0.5s;

        @media(max-width: $mobile-width) {
            background: $bg;
        }
    }

    &:hover::after {
        left: 50%;
        width: 0;
    }
}

.contacts-item__text {
    margin: 30px 0;
    font-size: 14px;
    line-height: 16px;

    @media(max-width: $mobile-width) {
        margin: 10px 0;
    }
}
