.games-item {
    position: relative;

    display: flex;

    margin-bottom: 25%;

    background-color: #11a9e6;
    border: 2px solid #fff;
    border-radius: 55px;

    &::before {
        content: "";

        position: absolute;
        bottom: 0;
        right: 0;

        width: 100%;
        height: 100%;

        background-image: url('../img/games/bubbles.png');
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        transform: translateY(100%) scale(-1, 1);

        @media(max-width: $mobile-width) {
            height: 70%;
        }
    }

    &:nth-child(even)::before {
        transform: translateY(100%);
    }
}

.games-item__image,
.games-item__content {
    display: flex;
    justify-content: center;
    align-items: center;
}

.games-item__image {
    position: relative;
    z-index: 1;

    padding-left: 45%;
}

.games-item__image img {
    position: absolute;
    bottom: 50%;
    left: 0;

    width: 100%;
    height: auto;

    transform: translateY(50%);
}

.games-item__content {
    position: relative;

    flex-direction: column;

    width: 50%;
    padding: 100px 10px 50px 10px;

    @media(max-width: $desktop-width) {
        padding: 20px;
    }

    @media(max-width: $mobile-width) {
        padding: 15px;
    }
}

.games-item--mansiontale {
    &::after {
        content: "";

        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 1;

        width: 229px;
        height: 187px;

        background-image: url("../img/games/new.png");
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;

        @media(max-width: $desktop-width) {
            width: 159px;
            height: 130px;
        }

        @media(max-width: $mobile-width) {
            left: auto;
            right: 0;

            width: 163px;
            height: 100px;

            background-position: top right;
            transform: rotate(-90deg) translate(-6%, 32%);
        }
    }
}

.games-item__content-text {
    margin: 0;
    margin-bottom: 0.5em;

    font-size: 29px;
    line-height: 1.1em;
    text-transform: uppercase;
    text-align: center;

    @media(max-width: $desktop-width) {
        font-size: 24px;
    }

    @media(max-width: $tablet-width) {
        font-size: 22px;
    }

    @media(max-width: $mobile-width) {
        font-size: 18px;
    }

    @media(max-width: $small-width) {
        font-size: 14px;
    }
}

.games-item__content-button {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 8em;
    height: 3em;

    background-image: url("../img/games/button.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    color: #fff;
    font-size: 32px;
    line-height: 1.3em;
    text-transform: uppercase;

    transition: 0.5s;

    @media(max-width: $desktop-width) {
        font-size: 24px;
    }

    @media(max-width: $mobile-width) {
        font-size: 20px;
    }

    @media(max-width: $small-width) {
        font-size: 12px;
    }

    &:hover {
        transform: scale(1.1);
    }
}
