.header {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;

    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    padding-top: 10px;
}

.header__logo {
    position: relative;

    display: flex;
    justify-content: flex-start;
    align-items: center;

    &::before {
        content: "";

        position: absolute;
        top: 15%;
        left: 0;
        z-index: -1;

        width: 100%;
        height: 309px;

        background-image: url('../../img/logo-before.png');
        background-repeat: no-repeat;
        background-position: left top;
        background-size: contain;
    }

    img {
        @media(max-width: $desktop-width) {
            height: 120px;
        }

        @media(max-width: $tablet-width) {
            height: 10vw;
        }
    }

    @media(max-width: $tablet-width) {
        &::before {
            top: 10%;
            left: -10%;

            background-image: url('../../img/logo-before-mobile.png');
        }
    }
}

.header-menu__list {
    position: relative;
    display: flex;

    margin: 0;
    padding: 0;

    list-style: none;

    @media(max-width: $mobile-width) {
        display: none;
    }
}

.header-menu__item {
    position: relative;

    margin: 0;
    margin-left: 40px;
    padding: 0;

    list-style: none;
    transition: 0.5s;

    &::after {
        content: "";

        position: absolute;
        bottom: 2px;
        left: 50%;

        width: 0;
        height: 2px;
        background-color: #3e6372;
        transition: 0.5s;
    }

    &:hover::after {
        left: 0;
        width: 100%;
    }

    a {
        color: #3e6372;
        font-size: 25px;
        text-transform: uppercase;
        text-shadow:
            6px 0px 6px #fff,
            0px 6px 6px #fff,
            -6px 0px 6px #fff,
            0px -6px 6px #fff;

        @media(max-width: $tablet-width) {
            font-size: 22px;
        }

        @media(max-width: $mobile-width) {
            font-size: 18px;
        }

        @media(max-width: $small-width) {
            font-size: 16px;
        }
    }
}

.header {
    &.active {
        width: 100vw;
        height: 100vh;

        justify-content: center;

        background-color: #fff;

        .header__logo {
            display: none;
        }

        .header-menu__list {
            display: flex;
            flex-direction: column;
        }
    }
}
