.contacts {
    margin-top: -6%;
    padding-top: 6%;

    background-image: url('../../img/contacts/bg.png');
    background-repeat: no-repeat;
    background-size: cover;

    @media(max-width: $tablet-width) {
        margin-top: -8%;
        padding-top: 8%;
    }

    @media(max-width: $mobile-width) {
        margin-top: -13%;
        padding-top: 13%;
    }
}

.contacts-list {
    display: flex;
    justify-content: space-around;

    margin: 0;
    margin-top: 75px;
    margin-bottom: 30px;
    padding: 0;

    list-style: none;

    @media(max-width: $mobile-width) {
        flex-direction: column;

        margin-top: 30px;
    }
}

.contacts-footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contacts-footer__links a {
    position: relative;

    color: #2b2b2b;
    font-size: 17px;
    text-transform: uppercase;
    text-decoration: none;

    @media(max-width: $mobile-width) {
        font-size: 15px;
        line-height: 18px;
    }

    &::after {
        content: "";

        position: absolute;
        bottom: 1px;
        left: 0;

        width: 100%;
        height: 2px;
        background-color: #2b2b2b;
        transition: 0.5s;
    }

    &:hover::after {
        left: 50%;
        width: 0;
    }
}

.contacts-footer__addres {
    color: #2b2b2b;
    font-size: 17px;
    text-decoration: underline;

    @media(max-width: $mobile-width) {
        font-size: 15px;
        line-height: px;
    }
}

.social-list {
    display: flex;

    margin: 25px 0;
    padding: 0;

    list-style: none;
}

.social-item {
    width: 55px;
    height: 55px;
    margin: 0 14px;

    background-size: cover;
    cursor: pointer;
    transition: 0.5s;

    @media(max-width: $mobile-width) {
        width: 45px;
        height: 45px;

        margin: 0 7px;
    }


    &:hover {
        transform: scale(1.1);
    }

    &.social-item__fb {
        background-image: url('../../img/contacts/fb.png');
    }

    &.social-item__inst {
        background-image: url('../../img/contacts/inst.png');
    }

    &.social-item__in {
        background-image: url('../../img/contacts/link.png');
    }

    a {
        display: block;
        width: 100%;
        height: 100%;
    }
}
